import { Controller } from '@ali/4ever-cangjie';
import toggleVertAlignMark from './toggleVertAlignMark';
import { getRangesFromEditor }  from '@ali/4ever-utils';

export default function toggleSubMark(controller: Controller) {
  const ranges = getRangesFromEditor(controller);
  return toggleVertAlignMark(controller, ranges, 'subscript');
}
